@import '../helpers/variables';

.header {
  width: 100%;
  height: 100px;
  position: fixed;
  top: 0;
  z-index: 100;
  background: #fff;
  box-shadow: 0 0 5px rgba(0,0,0,.5);

  .wrapper {
    max-width: 1280px;
    margin: 0 auto;
    display: flex;
    position: relative;
    align-items: center;
  }

  nav[role="main-navigation"] {
    height: 100px;
    flex: 1;

    a {
      height: 100px;
      padding: 0 10px;
      line-height: 100px;
      display: inline-block;
      font-weight: 600;
      text-transform: uppercase;
      color: $default-textcolor;
      text-decoration: none;

      &:hover,
      &.active {
        color: $orange;
      }
    }
  }

  .branding {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    text-indent: -9999px;
    position: absolute;
    left: 50%;
    margin-left: -100px;
    background: url('../img/LAVN_large.png') no-repeat 50% 50%;

    &.small {
      width: 100px;
      height: 100px;
      border-radius: 0;
    }
  }

  nav[role="user-navigation"] {
    height: 100px;
    padding-top: 33px;
    flex: 1;

    .wrapper {
      float: right; 
    }
  }

  nav[role="user-navigation"].logged-in {
    display: none;
  }
}


